form {
    width: 450px;
    min-height: 400px;
    border-radius: 5px;
    margin: 2% auto;
    box-shadow: 0 9px 50px hsla(214, 17%, 8%, 0.784);
    padding: 2%;
    background-color: #354152;
  }
  
  .App, body {
    background-size: cover;
    background-image: linear-gradient(to right, #354152, #6f7f98, #354152 );
    background-attachment: fixed;
    background-repeat: no-repeat;
    font-family: 'Vibur', cursive;
    opacity: .95;
    justify-content: center;
    display: flex;
    margin-top: 5%;
  }
  
  .auth-form ,.login-form, .register-form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  label {
    color:#8ceabb;
  }
  
  .box {
    justify-content: center;
    display: flex;
    flex-direction: column;
    align-items: left;
  }
  
  .form-header {
    font-size: 250%;
    font-family: 'Playfair Display', serif;
    color: #8ceabb;
    margin-bottom: 10%;
    margin-top: 10%;
  }
  
  input {
    margin: 0.5rem 0;
    padding: 1rem;
    border: none;
    border-radius: 10px;
    font-family: 'Vibur', cursive;
    margin-bottom: 5%;
  }
  
  .submit {
    outline: none;
    border: none;
    height: 50px;
    width: 40%;
    border-radius: 5px;
    margin-top: 3%;
    background-image: linear-gradient(160deg, #8ceabb 0%, #378f7b 100%);
    color:#354152;
    cursor: pointer;
  } 
  
  .link-btn {
    outline: none;
    border: none;
    height: 50px;
    width: 280px;
    letter-spacing: 0.05em;
    border-radius: 5px;
    margin: 5% auto;
    background: none;
    color: white;
    text-decoration: underline;
    cursor: pointer;
  }
  
  button:hover {
    transform: translatey(5px);
    box-shadow: none;
  }
  
  button:hover {
    animation: ani9 0.4s ease-in-out infinite alternate;
  }
  
  @keyframes ani9 {
    0% {
        transform: translateY(3px);
    }
    100% {
        transform: translateY(5px);
    }
  }
  